:root{
  --Red : #FF2D58;
  --lightestPink : #FFEFF3;
  --lightPink : #FEE8ED;
  --pink : #FFE1E8;
  --whiteBlur : rgba(255,255,255,0.65);
  --white:#FFFFFF;
  --lightGray: #ACACAC;
  --gray:#707070;
  --darkGray:#05070D;
  --darkNavyBlue: #2E266F;
  --navyBlue: #382C8E;
  --lightNavyBlue: #695EBC;
  --green:#4FE84A;
}
/* FontSize Start */
.FH1{
  font-size: 50px;
}
.FH2{
  font-size: 30px;
}
.FT1{
  font-size: 19px;
}
.FT2{
  font-size: 15px;
}
.FT3{
  font-size: 13px;
}
.FMH1{
  font-size: 40px;
}
.FMH2{
  font-size: 25px;
}
.FMT1{
  font-size: 18px;
}
.FMT2{
  font-size: 15px;
}
.FMT3{
  font-size: 13px;
}
/* FontSize End */
body {
  margin: 0;
  background-color: var(--lightestPink) !important;
  overflow-x: hidden !important;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p{
  margin: 0;
}
a{
  color: unset !important;
  text-decoration: none !important;
}

.Flex {
  display: flex;
}
.HCenter {
  justify-content: center;
}
.VCenter {
  align-items: center;
}
.Col {
  flex-direction: column;
}
.Row {
  flex-direction: row;
}
.Rtl {
  direction: rtl;
}
.Ltr {
  direction: ltr;
}
.TRight {
  text-align: right;
}
.TLeft {
  text-align: left;
}
.TCenter {
  text-align: center;
}
.TJus {
  text-align: justify;
}
.W100 {
  width: 100%;
}
.H100 {
  height: 100%;
}
.Pointer {
  cursor: pointer;
}
.Transition{
  transition: 0.5s;
}
.BgImg{
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}