/* Colors Vatiables Start */
:root{
  --Red : #FF2D58;
  --lightestPink : #FFEFF3;
  --lightPink : #FEE8ED;
  --Pink : #ffe1e8;
  --whiteBlur : rgba(255,255,255,0.65);
  --white:#FFFFFF;
  --lightGray: #ACACAC;
  --gray:#707070;   
  --darkGray:#05070D;
  --darkNavyBlueOpacity1: rgba(46, 38, 111,0.1);
  --darkNavyBlueBlur:rgba(46, 38, 111,0);
  --darkNavyBlue: #2E266F;
  --navyBlue: #382C8E;
  --lightNavyBlue: #695EBC;
  --green:#4FE84A;
}
body::-webkit-scrollbar-track
{
	border-radius: 10px;
  background-color: rgb(255, 45, 88,0.2);
  width:5px;
}

body::-webkit-scrollbar
{
	width:5px;
  background-color: rgb(255, 45, 88,0.2);
}

body::-webkit-scrollbar-thumb{
  background-color: var(--Red);
  /* background-image: url('./assets/images/scrollbar.png'); */
}
/* MyBtn Style Start */
.MyBtn{
  border-radius: 100px;
  text-align: center;
  padding: 15px;
  outline: none !important;
  box-shadow: none !important;
  border: none;
  cursor: pointer !important;
  position: relative;
  white-space: nowrap;
}
.MyTagBtn{
  background-color: transparent !important;
  border-style:solid !important;
  border-width: 1.5px !important;
  padding: 12px 26px !important;
}

.MyBlurBtn{
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
.MyLikeBtn{
  padding: 9px !important;
}
.MyDiscountBtn{
  padding: 9px !important;
}
.MyRateBtn{
  padding: 5px !important;
  display: flex;
  flex-direction: row;
}
.MyRateBtn > div > img{
  width: 15px;
  height: 15px;
  top: calc(50% - 7.5px);
  left: calc(50% - 7.5px);
}
.MyDiscountBtn > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* MyBtn Style End */
/* MyIcon Style Start */
.MyIcon{
  height: 24px;
  width: 24px;
  position: absolute;
  top: calc(50% - 12px);
  left: calc(50% - 12px);
}
/* MyIcon Style End */
/* CategoryItem Style Start */
.CategoryItem{
  padding: 8px;
  border-radius: 100px;
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
  background-color: var(--white);
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  transition: 0.7s;
  cursor: pointer;
  position: relative;
  height: 125px;
}
.CategoryItem > p{
  color: var(--lightGray);
  margin: 10px 0px;
}
.CategoryItem > div > button{
  position: absolute;
  top: 5px;
  left: 8.79px;
  height: 56px;
  width: 56px;
  animation-name: CategoryItem;
  animation-duration: 0.7s;
  animation-direction : alternate;
  animation-fill-mode: forwards;
  -webkit-animation-name: CategoryItem;
  -webkit-animation-duration: 0.7s;
  -webkit-animation-direction : alternate;
  -webkit-animation-fill-mode: forwards;
}
.CategoryItemActive{
  background-color: var(--darkNavyBlue);
  flex-direction: column;
}
.CategoryItemActive > p{
  color: var(--white);
}
.CategoryItemActive > div > button{
  animation-name: CategoryItemActive;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  animation-direction : alternate;
  -webkit-animation-name: CategoryItemActive;
  -webkit-animation-duration: 0.7s;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-direction : alternate;
}
.CategoryItem > div > button > div > img{
  transition: 0.7s;
}
.CategoryItemActive > div > button > div > img{
  filter: brightness(0.5) sepia(5) hue-rotate(290deg) saturate(13);
  -webkit-filter: brightness(0.5) sepia(5) hue-rotate(300deg) saturate(13);
}
@keyframes CategoryItemActive {
  from {top: 5%;bottom:50%;}
  to {top:50%;bottom: 5%;}
}
@keyframes CategoryItem {
  from {top: 50%;bottom: 5%;}
  to {top: 5%;bottom: 50%;}
}
/* CategoryItem Style End */
/* RestaurantItemSlider Style Start */
.contentRestaurantItemSlider{
  background-color: var(--white);
  border-radius: 15px;
  width: calc(100% + 0.5px);
}
.DiscountedRestaurant > img{
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.contentRestaurantItemSlider > div{
  padding: 5px 20px;
  padding-bottom: 15px;
}
.contentRestaurantItemSlider > div > h1{
  color: var(--Red);
  margin: 20px 0px;
}
.contentRestaurantItemSlider > div > p{
  line-height: 7px;
}
.contentRestaurantItemSlider > div > small{
  color: var(--lightGray);
}
.RecommendedRestaurant > .contentRestaurantItemSlider > div {
  padding: 23px 20px;
}
.RecommendedRestaurant > img{
  border-radius: 15px;
}
.FilterRestaurant > .contentRestaurantItemSlider{
  border-radius: 25px;
}
.FilterRestaurant > .contentRestaurantItemSlider > div:first-child {
  padding: 30px 20px 20px 20px;
}
.FilterRestaurant > .contentRestaurantItemSlider > div:last-child {
  padding: 5px 20px;
  padding-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* RestaurantItemSlider Style End */
/* CounterProduct Style Start */
