/* Dropdown Style Start */
.dropdown{
  width: 100%;
  transition: 0.5s;
  margin-right: 10px;
  position: relative;
  min-width: 150px;
  z-index: 6;
}
.dropdownContainer{
  position: absolute;
  top: 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  border: 1.5px solid var(--darkNavyBlue);
  border-radius: 25px;
  opacity: 0.5;
  transition: 0.5s;
}
.dropdownContainer:hover{
  opacity: 1;
}
.dropdownContainerActive{
  border-radius: 25px;
  border: 0px solid var(--darkNavyBlue);
  background-color: var(--darkNavyBlue);
  opacity: 1;
  /* padding-top: 52px; */
}
.dropdownContainerActive::-webkit-scrollbar-track
{
	border-radius: 10px;
  background-color: transparent;
  width: 2px;
  margin: 20px;
}

.dropdownContainerActive::-webkit-scrollbar
{
	width: 5px;
  background-color: transparent;
}

.dropdownContainerActive::-webkit-scrollbar-thumb{
	border-radius: 10px;
  background-color: var(--lightNavyBlue);
  /* background-image: url('./assets/images/scrollbar.png'); */
  width: 20px;
}

.dropdownContainerDisable{
  cursor: default !important;
  border: 1.5px solid var(--lightGray) !important;
  opacity: 0.5 !important;
}
.dropdownContainerDisable > div:first-child > .dropdownContent > small{
  color: var(--lightGray);
}
.dropdownContainerDisable > div:first-child > .dropdownArrow > img{
  filter: brightness(1);
  -webkit-filter: brightness(1);
  opacity: 0.5;
}
.dropdownItemsActive > div:first-child > small{
  color: var(--white);
}
.dropdownItemsActive > div:last-child > img{
  filter: brightness(100);
  -webkit-filter: brightness(100);
  transform:rotateZ(180deg);
  -webkit-transform:rotateZ(180deg);
  display: none;
}
.dropdownItemsActive{
  display: flex !important;
}
.dropdownItemsActive:hover{
  background-color: var(--Red);
}
.dropdownItemsActive:first-child:hover{
  background-color: transparent;
}
.dropdownItemsActive:hover > div > small{
  color: var(--white) !important;
}
.dropdownContainerActive > .dropdownItemsActive:first-child > div:last-child > img{
  display: block;
}
.dropdownContainerActive > .dropdownItemsActive:first-child > div:first-child > small{
  color: var(--white);
}
.dropdownContainerActive > .dropdownItemsActive > div:first-child > small{
  color: var(--lightNavyBlue);
}
.dropdownContent > small{
  color: var(--darkNavyBlue);
}
.dropdownItems{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding: 14px 19px;
  border-radius: 25px;
  display: none;
}
.dropdownItems:first-child{
  margin-top: 0px;
  display: flex;
  top: 0px;
  left: 0px;
}
.dropdownArrow > img{
  width: 10px;
  height: 10px;
  margin-left: 10px;
  transition: 0.5s;
}
