  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  padding-top: 10%;
  padding-left: 30px;
  padding-right: 30px;
}
/* UserRegistrarionSView Style End */
/* RestaurantSView Style Start */
.ResturantLogoSView{
  width: 100%;
  height: 100%;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
}
.RestaurantInfoSView{
  background-color: var(--lightPink);
  border-bottom-left-radius: 35px;
  border-bottom-right-radius: 35px;
  padding: 25px 0px;
}
/* RestaurantSView Style End */
/* Dashboard Style Start */
.DashboardTabSView{
  padding: 20px 25px 20px 25px;
  background-color: var(--white);
  border-radius: 35px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  margin-left: 5%;
  margin-top: 25px;
  width: 95%;
  transition: 0.5s;
}
.DashboardTabSView:hover body{
  overflow-y: hidden;
}
.DashboardTabSView::-webkit-scrollbar-track
{
  width: 1px;
  height: 1px;
}

.DashboardTabSView::-webkit-scrollbar
{
	width: 1px;
  height: 1px;
}

.DashboardTabSView::-webkit-scrollbar-thumb{
  width: 1px;
  height: 1px;
  background-color: transparent;
}
.itemDashboardSView{
  margin-right: 20px;
  display: flex;
  flex-direction: column-reverse;
  cursor: pointer;
}
.itemDashboardSView  > div{
  height: 3.5px;
  width: 100%;
  border-radius: 7px;
  background-color: var(--Red);
  transform-origin: center;
  -webkit-transform-origin: center;
  transform:rotateY(90deg);
  -webkit-transform:rotateY(90deg);
  transition: 0.5s;
}
.itemDashboardSView > small{
  white-space: nowrap;
  color: var(--lightGray);
  padding: 1px 0px;
}
.itemDashboardSViewActive > div{
  height: 3.5px;
  border-radius: 7px;
  width: 100%;
  background-color: var(--Red);
  transform:rotateY(0deg);
  -webkit-transform:rotateY(0deg);
}
.itemDashboardSViewActive > small{
  color: var(--Red) !important;
}
/* Dashboard Style End */
/* FavoriteItemSView Style Start */
.FavoriteItemSView{
  width: 100%;
  justify-content: center;
  margin-top: 15px;
}
.FavoriteItemSViewContainer{
  background-color: var(--Pink);
  width: 90%;
  padding: 20px;
  border-radius: 25px;
}
.FavoriteItemSViewContainer > div > img{
  width: 100%;
}
/* FavoriteItemSView Style End */

